<template>
	<div class="card-view" :style="{ borderColor: borderColor }">
		<div class="card-content" :style="{ backgroundColor: color }">
			<div class="attack-item">
				<div class="attack-id">{{ content.id }}</div>
				<div class="attack-name">{{ content.name }} <b>{{ count > 0 ? `(${count})` : '' }}</b></div>
				<div class="attack-describe">{{ content.describe }}</div>
			</div>
			<div class="more" v-if="content.extend.length > 0" @click="showExtend = !showExtend">
				<i class="iconfont" :class="showExtend ? 'icon-shouqi' : 'icon-zhankai'" />
			</div>
		</div>
		<ul class="card-extend" v-if="showExtend">
			<li class="attack-item" v-for="(item, index) in content.extend" :key="index" :style="{ borderColor: borderColor }">
				<div class="attack-name">{{ item.name }}</div>
				<div class="attack-describe">{{ item.describe }}</div>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'AttackCard',
	props: {
		content: Object,
		color: {
			type: String,
			default: 'none',
		},
		borderColor: {
			type: String,
			default: 'none',
		},
	},
	data() {
		return {
			showExtend: false,
			count: 0,
		};
	},
	mounted() {
		if (this.content.extend && this.content.extend.length > 0) {
			this.count = this.content.extend.length;
		}
	},
};
</script>

