ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
}
.card-view {
	border-width: 1px;
	border-style: solid;
	.card-content {
		line-height: 26px;
		.more {
			border-top: 1px dashed #000;
			text-align: center;
			cursor: pointer;
		}
		.iconfont {
			-webkit-user-select:none;
			-moz-user-select:none;
			-ms-user-select:none;
			user-select:none;
			font-size: 16px;
			font-weight: bold;
		}
	}
	.card-extend {
		li {
			border-top-width: 1px;
			border-top-style: solid;
		}
	}
}
