<template>
	<div class="attack-card-group" v-for="(attack, index) in attacks" :key="index">
		<div class="title" :style="{ backgroundColor:attack.color }">
			<div>{{ attack.name }} ({{ attack.item.length }})</div>
			<div>{{ attack.describe }}</div>
		</div>
		<AttackCard class="card" :color="attack.itemColor" :borderColor="attack.color" v-for="(item, key) in attack.item" :content="item" :key="key" />
	</div>
</template>

<script>
import axios from 'axios';
import AttackCard from '@/components/AttackCard.vue';

export default {
	name: 'Attack',
	components: {
		AttackCard,
	},
	data() {
		return {
			attacks: [],
		};
	},
	async created() {
		const result = await axios.get('/json/attack.json');
		this.attacks = result.data;
	},
};
</script>

<style lang="less" scoped>
.attack-card-group {
	min-width: 200px;
	margin-left: 4px;
	.title {
		padding: 10px;
		text-align: center;
		border: 1px solid #000;
		font-weight: bold;
	}
	.card {
		margin-top: 4px;
	}
}
.attack-card-group:last-child {
	margin-right: 4px;
}
</style>
